// hover
.navbarDown_detail:hover {
    color: $primary !important;   
}
// active
.navbarDown_detail .active {
  color: $primary !important;  
}

// mobile-mx(767)
@include mobile-mx{
  .nav-scroller{
    height: 3.3125rem;        
    overflow-y: hidden;     /*  */
    border-bottom: 1px solid $wordColor_1;  /**/
  }
  .nav-scroller .nav{
    display: flex;
    flex-wrap: nowrap;
    padding-bottom: 1rem;  /*  */
    overflow-x: auto;
    white-space: nowrap;  /*  */
  }
}

