//  design.html 
.img-position-center {
  position: relative;
  overflow: hidden;
  img{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover;
  }
}

//  formValidation.js and modal.ejs
.disabled-style {
  width: 100%;
  height: 50px;
  position: absolute;
  cursor: not-allowed;
  background: rgba($light, .5);
}