.bg-cover {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

// img
.object-fit-cover {
  object-fit: cover;
  object-position: right bottom; //
  height: inherit;
}

//line-height 
.lh-0{
  line-height: 0;
}

//hover
.useHover_1{
  &:hover {
    color: $secondary !important;
    border-bottom: 1px solid $secondary;
  }
}
.useHover_1{
  border-bottom: 1px solid transparent;
}

// nav-scroller.ejs hover 
// detail.html    hover 
.border__bottom__hover{
  position: relative;

  &::before {
    content: '';
    position: absolute;
    width: 0%;
    height: 1px;
    background: $secondary;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    transition-duration: .2s;
    opacity: 0;
  }

  &:hover {
    &::before{
      width: 100%;
      opacity: 1;
    }
  }
  &--checked{
    &::before{
      width: 100%;
      opacity: 1;
    }
  }
}

// swiper.ejs Hover 
.colorHover{
  &--secondary{
    color: #000;
    &:hover{
      color: $secondary !important;
    }
  }
}

// toUpBtn.ejs hover
.transform-y-1{
  &:hover{
    transform: translateX(-5px);
    color: $primary;
  }
}

.transition-duration-1{
  transition-duration: .2s;
}

//  "^"  
.bg-secondary{
  background-color: $secondary !important;
}

